.dialogs-items {
  margin-right: 10px;
}
.dialog-item {
  background-color: #f3f8f8;
  padding: 15px;
  border: 3px solid #d5c5e0;
  margin-bottom: 5px;
  border-radius: 10px;

  font-size: 14pt;
  font-weight: 600;
}
.dialog-item:hover {
  background-color: #fcebfc;
}
.messages-items {
  border: 3px solid #d5c5e0;
  margin-bottom: 5px;
  border-radius: 10px;
  background-color: #f3f8f8;
}
.message-item {
  padding: 15px;
}
.dialogs-menu {
  display: grid;
  grid-template-columns: 2fr 10fr;
}
