.post_item {
  border: 4px solid #9db8f9;
  border-radius: 10px;
  padding: 10px;
  font-size: 20pt;
  margin: 30px 0 0 0;
}
.like {
  border: 4px solid #9db8f9;
  border-radius: 10px;
  padding: 5px;
  background-color: #f3a7ef;
  color: white;
  margin: 10px;
}

.img_post {
  margin: 30px 10px -20px -15px !important;
  border: 4px solid #9db8f9;
}
